<script>
    import Player from './Player.vue'
    import { QClient } from "../mqtt_connect.js"

    export default {
        name: 'Manual',
        components: {
            Player
        },
        props: {
            showDialog: {
                type: Boolean,
                default: false
            }
        },
        data: function () {
            return {
                boolean: false,
                qClient: new QClient(),
                radio1: "",
                ledTarget: `/lit3d/led/video-play`
            }
        },
        methods: {
            runVideoSmall: function(lineNum, index) {
                //let lineNum = Math.ceil(index / 2)
                //let playFlag = "0";
                // if(this.$refs["p"+index].icon == "el-icon-video-play") {
                //     this.$refs["p"+index].icon = "el-icon-video-pause"
                // }
                // else {
                //     this.$refs["p"+index].icon = "el-icon-video-play"
                // }
                let options = {
                    id: index,
                    muted: true
                }
                this.qClient.set(`/lit3d/slave/${lineNum}/ss-play`, JSON.stringify(options))
            },
            getMessage: function(topic, message, packet) {
                console.log(topic, message, packet)
            },
            runVideoBig(src) {
                let options = {
                    src: src,
                    muted: false
                }
                this.qClient.set(`/lit3d/led/video-play`, JSON.stringify(options))
            }
        },
        mounted() {
            this.qClient.client.on("message", this.getMessage)
            for(let i=1;i<=12;i++) {
                this.qClient.client.subscribe(`/lit3d/line/${i}`, {qos: 0})
            }

        }
    }
</script>

